@use 'base';

// $height-of-hero: calc(.hero-content.height)
%mar-bt-1e{
    margin-bottom: 1em;
}

.hero{
    display: flex;
    flex-direction: column;
}

.hero-content{
    padding: 2em;
    width: 95%;
    margin: 0 auto;
}
.descrip, .sneak-comp, .heading{
    @extend %mar-bt-1e;
}
.sneak-comp{
    color: base.$theme;
    font-weight: bold;
}


.descrip{
    color: rgb(96, 96, 96);
    line-height: 1.7em;
    // text-align: justify;
}

.heading, .offer-price{
    font-size: 2rem;
    font-weight: bold;
}

.pric-per-apr{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.price-perc{
    display: flex;
    // justify-content: space-between;
    align-items: center;
    gap: 1em
}

.offer-perc{
    color: base.$theme;
    font-weight: bold;
}

.price{
    display: inline;
    color: rgb(176, 176, 176);
    text-decoration: line-through;
    font-weight: bold;
    justify-self: flex-start;
}

.cart-num{
    display: flex;
    justify-content: space-between;
    margin:2em 0;
}

.cart-buttons{
    border: none;
}

.add-cart{
    background-color: base.$theme;
    border: solid base.$theme;
    padding: 1em;
    text-align: center;
    color: white;
    display: flex;
    gap: 1em;
    justify-content: center;
    align-items: center;
    border-radius: 1em;
    margin-bottom: 4em;

    &:hover{
        background-color: white;
        color: base.$theme;

        svg path{
            fill: base.$theme;
        }
    }
}


@media only screen and (min-width: 850px){
    .hero{
        flex-direction: row;
        max-width: 1200px;
        margin: 0 auto;
        margin-top: 3em;
    }

    .hero-content{
        width: 100%;
        padding: 0 4em;
    }

    .pric-per-apr{
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
    }

    .cart-num_cart-button{
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1em;
    }

    .cart-num{
        display: flex;
        // justify-content: space-around;
        align-items: center;
        gap: 1em
    }

    .add-cart{
        align-items: center;
        margin: unset;
        width: 100%;
    }

}